import {TRAER_POKEMONS, BUSCAR_POKEMONS, SORT, SORT_FORCE, FILTER, TRAER_TIPOS} from '../actions/index.js'

const initialState = {
    pokemons: [],
    filtroPokemons: [], //allCharacters
    tipos:[]
}

export default function pokemonReducer(state = initialState, action){

    switch (action.type) {
        case TRAER_POKEMONS:
            return {
                ...state,
                pokemons: action.payload,
                filtroPokemons: action.payload
            }
        case TRAER_TIPOS:
            return{
                ...state,
                tipos: action.payload
            }
        case BUSCAR_POKEMONS:
            return {
                ...state,
                filtroPokemons: action.payload
            }
        case SORT:
            let ordenarPokemons = [...state.pokemons]
            
            ordenarPokemons = ordenarPokemons.sort((a,b)=>{
                if(a.name < b.name){
                    return action.payload === "ascendente"? -1 : 1;
                }
                if (a.name > b.name) {
                    return action.payload === "ascendente"? 1 : -1;
                }
                return 0
            })
            return {
                ...state,
                filtroPokemons: ordenarPokemons
            }
        case SORT_FORCE:
            let ordenarPorFuerza = [...state.pokemons]
            
            ordenarPorFuerza = ordenarPorFuerza.sort((a,b)=>{
                if(a.force < b.force){
                    return action.payload === "Menor a Mayor"? -1 : 1;
                }
                if (a.force > b.force) {
                    return action.payload === "Menor a Mayor"? 1 : -1;
                }
                return 0
            })
            return {
                ...state,
                filtroPokemons: ordenarPorFuerza
            }
            case FILTER:
                let key= action.payload.key
                let value = action.payload.value
                const newPokemons = state.filtroPokemons.filter((pokemon) =>{
                    return pokemon[key] === value
                })
                return{
                    ...state,
                    pokemons: newPokemons
                    // filtroPokemons
                }
            
    
        default:
            return state;
    }

}